const addImage = (img) => {
  setImages(imgs => [...imgs, img])
}

const onChangeFile = async (event) => {
  event.stopPropagation();
  event.preventDefault();
  setImages([]);
  const _files = event.target.files;
  const files = [].slice.call(_files)
  .sort((a,b) => {
    if(Number.isInteger(a) && Number.isInteger(b)) {
      return a - b;
    }
    return a.name.localeCompare(b.name);
  });
  await uploadFile(files);
}

  const onDropImages = async (event) => {
    event.preventDefault();
    setImages([]);
    const _files = event.dataTransfer.files;
    const files = [].slice.call(_files)
    .sort((a,b) => {
      if(Number.isInteger(a) && Number.isInteger(b)) {
        return a - b;
      }
      return a.name.localeCompare(b.name);
    });
    await uploadFile(files);
  }

  const uploadFile = async (files) => {
    const supportedFilesTypes = ['image/jpeg', 'image/png'];
    for (let i=0;i<files.length;i++) {
      const file = files[i];
      if(i == 0) {
        if(supportedFilesTypes.indexOf(file.type) > -1) {
          const img = await readFileAsDataURL(file);
          setTitle(img);
        }
        continue;
      }
      if(supportedFilesTypes.indexOf(file.type) > -1) {
        // const reader = new FileReader();
        // reader.onload = e => addImage(e.target.result);
        const img = await readFileAsDataURL(file);
        addImage(img);
      }
      }
  }


